* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  
  body {
    margin: 0;
    font-family: "Roboto", Arial !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #d5d4d4;
  }
  
  p {
    font-size: 1.6rem;
  }
  
  h1 {
    font-size: 6rem;
  }
  
  .app {
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: rgba(0, 0, 0, 0.4);
    color: #fff;
  }
  
  .app:before {
    content: '';
    background: url(./assets/sunset.jpeg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
  }
  
  .container {
    max-width: 700px;
    margin: auto;
    padding: 0 1rem;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .extra {
    background-color: #00000017;
    border-radius: 10px;
  }
  
  .app .top {
    width: 100%;
    margin: 1rem auto;
  }
  
  .app .description {
    position: relative;
    right: -90%;
    transform-origin: 0 0;
    transform: rotate(269deg);
  }
  
  .app .bottom {
    margin-top: 200px !important;
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .bold {
    font-weight: 700;
  }

  .celcius {
    font-size: 60px;
  }
  
  
  /* searchBar */
  
  .app .search {
    text-align: center;
    padding: 1rem;
  }
  
  .app input {
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 25px;
    border: 1px solid rgba(255, 255, 255, 0.8); 
    background: rgba(255, 255, 255, 0.1);
    color: #f8f8f8f8;
  }
  
  ::placeholder {
    color: #f8f8f8;
  }



  @media only screen and (max-width: 600px) {
    .container {
      margin: 0px 10px;
    }

    .bottom > div p {
      font-size: 18px;
    }

    .bottom {
      justify-content: space-between !important;
    }
  }